import { ActivatedRoute } from '@angular/router';
import { DetailsService } from './../../../../services/details.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-trending-tv-show-details',
  templateUrl: './trending-tv-show-details.component.html',
  styleUrls: ['./trending-tv-show-details.component.scss']
})
export class TrendingTvShowDetailsComponent implements OnInit {
  tvShowId: number = 0;
  tvShowDetails: any;
  imgPrefix: string = "https://image.tmdb.org/t/p/w500"
  constructor(private DetailsService:DetailsService, private ActivatedRoute:ActivatedRoute) {
    this.tvShowId = this.ActivatedRoute.snapshot.params.id
  }

  ngOnInit(): void {
    this.DetailsService.getTvShowDetails(this.tvShowId).subscribe((data:any)=>{
      this.tvShowDetails = data
    })
  }

}
