import { ActivatedRoute } from '@angular/router';
import { DetailsService } from './../../../../services/details.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-actor-details',
  templateUrl: './actor-details.component.html',
  styleUrls: ['./actor-details.component.scss']
})
export class ActorDetailsComponent implements OnInit {
  actorId: number = 0;
  actorDetails: any;
  imgPrefix: string = "https://image.tmdb.org/t/p/w500"
  constructor(private DetailsService:DetailsService,private ActivatedRoute:ActivatedRoute) {
    this.actorId = this.ActivatedRoute.snapshot.params.id
   }

  ngOnInit(): void {
    this.DetailsService.getActorDetails(this.actorId).subscribe((data:any)=>{
      this.actorDetails = data
    })
  }

}
