import { ActorDetailsComponent } from './components/trending/trending-actors/actor-details/actor-details.component';
import { TrendingTvShowDetailsComponent } from './components/trending/trending-tv-shows/trending-tv-show-details/trending-tv-show-details.component';
import { MovieDetailsComponent } from './components/trending/trending-movies/movie-details/movie-details.component';
import { TrendingComponent } from './components/trending/trending.component';
import { AuthenticationComponent } from './components/authentication/authentication.component';
import { BookingComponent } from './components/booking/booking.component';
import { AboutComponent } from './components/about/about.component';
import { NotFoundComponent } from './components/not-found/not-found.component';
import { HomeComponent } from './components/home/home.component';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {path:'', redirectTo:'home',pathMatch:'full'},
  {path:'home', component:HomeComponent},
  {path:'about', component:AboutComponent},
  {path:'booking', component:BookingComponent},
  {path:'trending', component:TrendingComponent},
  {path:'moviedetails/:id', component:MovieDetailsComponent},
  {path:'trendingtvshowdetails/:id', component:TrendingTvShowDetailsComponent},
  {path:'actordetails/:id', component:ActorDetailsComponent},

  {path:'authentication', component:AuthenticationComponent},
  {path:'**', component:NotFoundComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
